// ];

type ExpandData = {
  visible: boolean;
  title: string;
  description: string;
  list: FabricRelatesResponse[];
};

const Component = defineComponent({
  name: "FabricDetail",
  components: {
    CreateProjectDialog,
    AddToCollectionDialog,
    DoubleFabricCard,
    FabricCard,
    FabricImageWith3DMode,
    WantToSeeMore
  },
  props: {
    id: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const router = useRouter();
    const store = useStore();
    const createProjectDialog: Ref<iCreateDialog | null> = ref(null);
    const addToCollectionDialog: Ref<iCreateDialog | null> = ref(null);

    const statusValue = ref("");
    const selectedId = ref(-1);

    const { id } = toRefs(props);

    const dialog = reactive<ExpandData>({
      visible: false,
      title: "",
      description: "",
      list: []
    });
    const mainFile = ref("");

    const detail = computed<FabricDetailResponse>(
      () => store.state.fabric.detail
    );
    const relates = computed<FabricRelatesResponse[]>(
      () => store.state.fabric.relates
    );
    const relatedCollections = computed<FabricRelatedCollectionResponse[]>(
      () => store.state.fabric.relatedCollections
    );
    const relatedProjects = computed<Project[]>(
      () => store.state.fabric.relatedProjects
    )

    const init = async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetFabric, id.value);
        await store.dispatch(GetFabricRelates, id.value);
        await store.dispatch(GetFabricRelatedCollections, id.value);
        await store.dispatch(GetFabricRelatedProject, id.value)
        statusValue.value = detail.value.status;
        mainFile.value = detail.value.media.length
          ? detail.value.media[0].file
          : "";
        selectedId.value = detail.value.media.length
          ? detail.value.media[0].id
          : 0;
      } catch (_) {
        await router.replace({ name: "Error" });
      } finally {
        loadingInstance.close();
      }
    };

    watch(id, init);

    onMounted(init);

    const getSecondaryMedia = (detail: FabricDetailResponse) => {
      return detail.media.reduce(
        (result, item: FabricFileResponse, index: number) => {
          if (index % 3) {
            (result[result.length - 1] as FabricFileResponse[]).push(item);
          } else {
            (result as FabricFileResponse[][]).push([item]);
          }
          return result;
        },
        []
      );
    };

    const onCopyLinkClick = () => {
      const href = window.location.href;
      copyToClipboard(href);
      ElNotification({
        title: "Success",
        type: "success",
        message: `Your Copied Link ${href}`
      });
    };

    const onAddToFavoriteClick = async () => {
      const { status, message } = await store.dispatch(
        PostAddFabricToFavorite,
        id.value
      );
      if (status === "success") {
        store.commit(SetFabricDetail, {
          ...detail.value,
          is_favorite: message.includes("add")
        });
      } else {
        ElNotification({
          title: "Error",
          type: "error",
          message
        });
      }
    };

    const onEditClick = async () => {
      await router.push({ name: "FabricEdit", params: { id: id.value } });
    };

    const onCreateProjectClick = async () => {
      if (!createProjectDialog.value) return;
      createProjectDialog.value.open(detail.value);
    };

    const onCarouseItemClick = (item: FabricFileResponse) => {
      mainFile.value = item.file;
      selectedId.value = item.id;
    };

    const onDownloadClick = (url: string) => {
      window.open(url, "blank");
    };

    const onCollectionClick = async (id: number) => {
      await router.push({ name: "CollectionDetail", params: { id } });
    };

    const onProjectClick = async (id: number) => {
      await router.push({ name: "ProjectDetail", params: { id } });
    };

    const onPlusClick = () => {
      if (!addToCollectionDialog.value) return;
      const params: CollectionAddFabric = {
        id: parseInt(id.value, 10),
        price: 0
      };
      addToCollectionDialog.value.open(params);
    };

    const onExpandClick = (data: ExpandData) => {
      dialog.visible = data.visible;
      dialog.title = data.title;
      dialog.description = data.description;
      dialog.list = data.list;
    };

    return {
      createProjectDialog,
      addToCollectionDialog,

      dialog,
      detail,
      relates,
      relatedCollections,
      relatedProjects,
      mainFile,
      statusValue,
      selectedId,

      onCopyLinkClick,
      onAddToFavoriteClick,
      onCreateProjectClick,
      onEditClick,
      onCarouseItemClick,
      onDownloadClick,
      onCollectionClick,
      onProjectClick,
      onPlusClick,
      onExpandClick,
      getSecondaryMedia,
      _
    };
  }
});

export default Component;
